.itemBotonHeaderFinalizar {
    text-align: center;
    background-color: #2c2c2c;
    border-color: white;
    border-width: 5px;
    border-top: 0;
    margin-bottom: 5px !important;
    border: 0;
}

.itemBotonFinalizar {
    display: flex;
    justify-content: center;
    background-color: #f1eeee;
    border-color: white;
    border-width: 5px;
    border-top: 0;
    margin-bottom: 5px !important;
    border: 0;
}

.botonFinalizar {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 5% 0 5%;
    font-size: 11px;
    width: 60%;
    height: 90%;
    background-color: #2c2c2c;
}

.botonFinalizar::after {
    content: "";
    display: block;
    height: 60%;
    overflow: hidden;
    background-image: url("../../img/arrow-right-short.svg");

    transition: width 200ms, margin-left 200ms;
}

.botonFinalizar:is(:hover, :focus)::after {
    width: 20%;
    margin-left: 8px;
}

.finalizado {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    background-color: #e5e3e3;
    color: black;
    font-size: 11px;
    border-radius: 0.25rem ;
}